import React, { Component } from 'react';
import styled from 'styled-components';
import TableFattura from './tableFattura';
import FattBodyFooter from './fattBodyFooter';

const FattBodyPart = styled.div`
  display: flex;
  border-top: 2px solid #333;
  border-bottom: 2px solid #333;
  padding: 20px 0;
  flex-direction: column;
  margin: 20px 0;
`;
const FattTitle = styled.h2`
  margin: 0 0 5px;
`;

class FattBody extends Component {
  render() {
    const json = this.props.json;

    let fatturaBody;
    if (json.hasOwnProperty('P:FatturaElettronica')) {
      fatturaBody = json['P:FatturaElettronica'].FatturaElettronicaBody[0];
    } else if (json.hasOwnProperty('FatturaElettronica')) {
      fatturaBody = json['FatturaElettronica'].FatturaElettronicaBody[0];
    }

    const dettaglioLinee = fatturaBody.DatiBeniServizi[0].DettaglioLinee;

    return (
      <FattBodyPart>
        <FattTitle>PRODOTTI E SERVIZI</FattTitle>
        <TableFattura info={dettaglioLinee} />
        <FattBodyFooter info={json} />
      </FattBodyPart>
    );
  }
}

export default FattBody;
