import React, { Component } from 'react';
import Viewer from './components/viewer';
import Uploader from './components/uploader';
import styled from 'styled-components';
import './App.css';

var xml2js = require('xml2js');

const ErrorUpload = styled.div`
  display: flex;
  flex-direction: column;
  width: 80vw;
  margin: 0 auto;
  min-height: 200px;
  align-items: center;
  justify-content: center;
`;

const ButtonReset = styled.button`
  margin: 20px 0 0;
  border: 1px solid #333;
  padding: 10px 30px;
  font-size: 1.2rem;
  background: #333;
  color: #fff;
  cursor: pointer;
`;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      file: '',
      isXML: true
    };
  }

  // componentDidMount() {
  //   this.setState({
  //     file: '',
  //     isXml: true
  //   });
  // }

  parseXML = file => {
    let resultFile;
    xml2js.parseString(file, {}, function(err, result) {
      if (err) {
        resultFile = '';
      }
      if (result) {
        resultFile = result;
      }
    });
    return resultFile;
  };

  onDrop = acceptedFiles => {
    acceptedFiles.forEach(file => {
      const reader = new FileReader();
      reader.onload = () => {
        //const file = JSON.parse(reader.result);
        console.log('file', reader.result);
        if (file.type === 'text/xml') {
          let fileResult = reader.result;
          fileResult = fileResult.replace(/[ï»¿]/g, '');
          let resultXML = this.parseXML(fileResult);
          console.log('resultXML', resultXML);
          this.setState({
            file: resultXML,
            isXML: true
          });
          //console.log(file);
        } else {
          this.setState({
            isXML: false
          });
        }
      };
      reader.onabort = () => console.log('file reading was aborted');
      reader.onerror = () => console.log('file reading has failed');

      reader.readAsBinaryString(file);
    });
  };
  restartUpload = () => {
    this.setState({
      file: '',
      isXML: true
    });
  };
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1>Visualizza la Fattura Elettronica</h1>
        </header>
        {!this.state.file && this.state.isXML && (
          <Uploader onDrop={e => this.onDrop(e)} />
        )}
        {!this.state.isXML && (
          <ErrorUpload>
            Non è un file XML.
            <ButtonReset onClick={e => this.restartUpload(e)}>
              Riprova
            </ButtonReset>
          </ErrorUpload>
        )}
        {this.state.file &&
          (!this.state.file.hasOwnProperty('P:FatturaElettronica') &&
            !this.state.file.hasOwnProperty('FatturaElettronica')) && (
            <ErrorUpload>
              Il File XML non è una Fattura Elettronica regolare.
              <ButtonReset onClick={e => this.restartUpload(e)}>
                Riprova
              </ButtonReset>
            </ErrorUpload>
          )}
        {this.state.file &&
          (this.state.file.hasOwnProperty('P:FatturaElettronica') ||
            this.state.file.hasOwnProperty('FatturaElettronica')) && (
            <Viewer file={this.state.file} />
          )}
      </div>
    );
  }
}

export default App;
